@media screen and (min-width: 320px) {
  header {
    width: 100%;
  }
  header img {
    height: 104px;
    margin: 92px auto 32px;
    width: 312px;
  }
}
@media screen and (min-width: 768px) {
  header img {
    height: 200px;
    margin: auto;
    margin-bottom: 16px;
    max-height: 200px;
    max-width: 600px;
    width: 100%;
  }
}
header {
  display: flex;
  flex-direction: column;
}/*# sourceMappingURL=header.css.map */