@media screen and (min-width: 320px) {
  .cart_container {
    display: flex;
    flex-direction: column;
    margin: auto;
    position: relative;
    width: 100%;
  }
  .cart_container .cart_go-back-button {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    cursor: pointer;
    font-size: 18px;
    font-weight: 700;
    line-height: 21.04px;
    position: absolute;
    left: 24px;
    top: 23px;
  }
  .cart_container .cart_go-back-button img {
    position: relative;
    top: 2px;
  }
  .cart_container .cart_image {
    border: 5px solid #F2F2F7;
    border-radius: 50%;
    height: 148px;
    width: 150px;
    margin: 124px auto 0;
  }
  .cart_container .cart_person-name {
    color: #081F32;
    font-size: 32px;
    font-weight: normal;
    line-height: 38px;
    margin: 24px auto;
    max-width: 312px;
    text-align: center;
    width: 100%;
  }
  .cart_container .cart_person-info {
    display: flex;
    flex-direction: column;
    margin: 16px auto;
    max-width: 312px;
    width: 100%;
  }
  .cart_container .cart_person-info h6 {
    color: #8E8E93;
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 0.15px;
    line-height: 24px;
    margin: 0;
    margin-bottom: 16px;
    vertical-align: middle;
    text-align: start;
  }
  .cart_container .cart_person-info .person-info_container {
    text-align: start;
  }
  .cart_container .cart_person-info .person-info_container .person-info_subtitle {
    color: #081F32;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 0.15px;
    line-height: 24px;
    margin: 0;
  }
  .cart_container .cart_person-info .person-info_container .person-info_subtitle::first-letter {
    text-transform: uppercase;
  }
  .cart_container .cart_person-info .person-info_container .person-info_field {
    color: #6E798C;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.25px;
    line-height: 20px;
    margin: 0;
  }
  .cart_container .cart_person-info .person-info_container .divider {
    border-top: 1px solid rgba(33, 33, 33, 0.08);
  }
}
@media screen and (min-width: 768px) {
  .cart_container .cart_go-back-button {
    left: 50px;
    top: 21px;
  }
  .cart_container .cart_go-back-button img {
    padding-right: 8px;
    position: relative;
    top: 2px;
  }
  .cart_container .cart_image {
    height: 300px;
    width: 300px;
    margin: 76px auto 0;
  }
  .cart_container .cart_person-name {
    font-size: 48px;
    line-height: 56px;
    margin: 16px auto;
    max-width: 500px;
  }
  .cart_container .cart_person-info {
    margin: 32px auto;
    max-width: 413px;
  }
  .cart_container .cart_person-info h6 {
    margin-bottom: 48px;
    text-align: center;
  }
}/*# sourceMappingURL=person-cart.css.map */